import styled, { css } from "styled-components";

import { colors, sizes, spacingMap } from "../styleguide";

import { StyledWorkspaceProps } from "./Workspace.types";

const fullScreenStyles = css`
	width: 100%;
	left: 0;
	height: 100%;
	top: 0;
	background-color: ${colors.white};
`;

export const StyledWorkspace = styled.div<StyledWorkspaceProps>`
	box-sizing: border-box;
	padding: ${spacingMap.sm} ${spacingMap.lg} ${spacingMap.lg} ${spacingMap.lg};
	overflow: auto;
	position: fixed;
	${({ zIndex }) => zIndex && `z-index: ${zIndex};`}

	width: calc(100% - ${sizes.sidebarAndPaddingWidth});
	left: calc(${sizes.sidebarWidth} + ${spacingMap.sm});
	height: calc(100% - ${sizes.menuBarHeightWithSpacing});
	top: ${sizes.menuBarHeightWithSpacing};
	background-color: transparent;

	${({ contentSize }) => contentSize === "fullScreen" && fullScreenStyles};
`;
